<template>
	<view >
	<patrolNavBar title="二维码巡检"></patrolNavBar>
	<map class="map"
		 id="map1"
	     :latitude="latitude"
		 :longitude="longitude"
		 :markers="markers"
		 :scale="scale"
		 :polyline="polyline"
		 enable-zoom="false"
		 :show-location="true"
		 @markertap="markertap">
	 <cover-view class="cover-view"  @click="onControltap()"></cover-view>
	</map>
	</view>
</template>
<script>
	import patrolNavBar from '../index/index.vue'
	export default{
		components:{patrolNavBar},
		data(){
			return{
				map1:0,
				title:'map1',
				latitude:this.latitude,
				longitude:this.longitude,
				scale:17,
				markers:[{},{},{}],
				polyline:[{
					points:[
						{latitude:this.latitude,longitude:this.latitude},
					],
				}],
			}
		},
		onLoad() {
			var that=this;
			this.getLocation();
			// #ifdef APP-PLUS
				const subNVue=uni.getSubNVueById('menu');
				subNVue.show();
			// #endif
		},
		methods:{
			//获取当前位置
			getLocation(){
				const that=this;
				uni.getLocation({
				    type: 'wgs84',
				    success: function (res) {
						that.latitude = res.latitude;
						that.longitude = res.longitude;
						that.polyline[0].points[0].latitude=res.latitude;
						that.polyline[0].points[0].longitude=res.longitude;
						let arr =[
						    {
						        id:1,
						        longitude:res.longitude,
						        latitude:res.latitude,
						        name:res.address
						    }
						]
						let markers = []
						for (var i=0;i<arr.length;i++){
						    markers=markers.concat({
						        id:arr[i].id,
						        latitude: arr[i].latitude,//纬度
						        longitude: arr[i].longitude,//经度
								iconPath: '../../static/png/zb.png',
								title:'巡检一号',
						    },
							{	
								id:2,
								latitude:37.865317,
								longitude:113.631957,
								iconPath: '../../static/png/mac.png',
								title:'巡检二号',
							},
							{
								id:3,
								latitude:37.862317,
								longitude:113.637957,
								iconPath: '../../static/png/mac.png',
								title:'巡检三号'
							},
							{
								id:4,
								latitude:37.861317,
								longitude:113.631957,
								iconPath: '../../static/png/mac.png',
								title:'巡检四号'
							},
							{
								id:5,
								latitude:37.862317,
								longitude:113.627957,
								iconPath: '../../static/png/mac.png',
								title:'巡检五号'
							},
							{
								id:6,
								latitude:37.865317,
								longitude:113.621957,
								iconPath: '../../static/png/mac.png',
								title:'巡检六号'
							},
							{
								id:7,
								latitude:37.865017,
								longitude:113.629957,
								iconPath: '../../static/png/mac.png', 
								title:'巡检七号'
							},
							{
								id:8,
								latitude:37.861317,
								longitude:113.621957,
								iconPath: '../../static/png/mac.png', 
								title:'巡检八号'
							},
							{
								id:9,
								latitude:37.863217,
								longitude:113.620957,
								iconPath: '../../static/png/mac.png', 
								title:'巡检九号'
							},
							{
								id:10,
								latitude:37.863517,
								longitude:113.615957,
								iconPath: '../../static/png/mac.png', 
								title:'巡检十号'
							})
						}
						that.markers=markers;
				    },
				});
			},
			onControltap(control) {
				   var that=this;
			      uni.createMapContext("map1", this).moveToLocation({
			        longitude: this.longitude,
			        latitude: this.latitude
				});
			},
			markertap: function(e) {
				let markers = [];
				// console.log(this.markers[e.detail.markerId-1]);
				//计算距离
				 var lon1 = (Math.PI / 180) * this.longitude;//开始经度
				 var lon2 = (Math.PI / 180) * this.markers[e.detail.markerId-1].longitude;//结束经度
				 var lat1 = (Math.PI / 180) * this.latitude;//开始纬度
				 var lat2 = (Math.PI / 180) * this.markers[e.detail.markerId-1].latitude;//结束纬度
				 // 地球半径
				 var R = 6371;
				 // 两点间距离 km，如果想要米的话，结果*1000就可以
				 var d = Math.acos(Math.sin(lat1) * Math.sin(lat2) + Math.cos(lat1) * Math.cos(lat2) * Math.cos(lon2 - lon1)) * R;
				 var d1=d*1000;
				 
				 //点击选择标记点
				 var that=this;
				 that.polyline = [{
				 points:[
				  {latitude:this.latitude,longitude:this.longitude},
				  {latitude:this.markers[e.detail.markerId-1].latitude,longitude:this.markers[e.detail.markerId-1].longitude},
				 ],
				 color:'#03ff81',//线的颜色
				 width:8,//线的宽度
				 dottedLine:false,//是否虚线
				 arrowLine:true,    //带箭头的线 开发者工具暂不支持该属性
				 arrowIconPath:'../../static/png/mac.png'
			}];
			},
		}
	}
</script>

<style>
.map{
	width: 750rpx;
	height: 1474rpx;
}
.cover-view {
	    width: 100rpx;
	    height: 100rpx;
	    border-radius: 60%;
		background: #ffffff;
	    position: absolute;
	    bottom: 23px;
	    right: 30px;
	  }
</style>
